@use "@/styles/mixins";

.filter__wrapper {
  z-index: 8;
  position: relative;
}

.dialog {
  &[open] {
    display: block;
    z-index: 1;
    height: 100vh;
    width: 100vw;

    display: flex;
    align-items: flex-end;
    justify-content: center;
  }

  border: 0;
  background-color: transparent;
  margin: 0;
  max-width: 100%;
  max-height: 100%;
  transition: all 200s linear;
  overflow: hidden;

  &::backdrop {
    background-color: var(--backdrop-bg);
    transition: all 200s linear;
  }
}

.dialog__element {
  border: 0;
  position: fixed;
  padding: 0px 0 40px 0;
  border-radius: 30px;
  background: var(--White, #fff);
  box-shadow: 0px 4px 250px 0px rgba(0, 0, 0, 0.25);
  align-items: stretch;
  top: 0;
  left: 0;
  right: auto;
  bottom: 0;
  width: 100%;
  max-width: calc(100% - 44px);
  max-height: calc(100svh - 106px);
  overflow: auto;
  margin: 1rem 22px 90px 22px;
  height: calc(100% - 110px);
  will-change: auto;
  animation: dialog-slide-in 200ms linear reverse normal;
  position: relative;
  height: fit-content;
  border-radius: 8px;
  border: 1px solid var(--White-30, rgba(247, 247, 247, 0.3));
  background: linear-gradient(
    180deg,
    rgba(247, 247, 247, 0.8) 0%,
    rgba(247, 247, 247, 0.5) 25.18%
  );
  backdrop-filter: blur(16px);

  .dialog[open] & {
    animation: dialog-slide-in 200ms linear forwards normal;
  }

  @include mixins.md {
    width: 720px;
    margin: 1rem 0 145px 130px;
    max-height: calc(100vh - 165px);
  }

  @include mixins.lg {
    margin: 1rem 0 145px 160px;
  }
}

.dialog__header {
  padding: 32px 32px 32px 52px;
  color: var(--black);
  display: flex;
  justify-content: space-between;

  button {
    height: 24px;
    width: 24px;
  }

  h2 {
    color: var(--black);
  }
}

.dialog__content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.dialog__list {
  list-style: none;
  width: 100%;
  padding: 0 16px;
  display: flex;
  flex-direction: column;
  gap: 4px;

  li {
    width: 100%;
  }

  @include mixins.md {
    padding: 0 52px;
  }
}

.option {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
  color: var(--black);
  width: -webkit-fill-available;
  padding: 20px 24px;
  transition: all 200ms cubic-bezier(0.23, 1, 0.32, 1);
  border-radius: 4px;
  background: var(--white);

  svg {
    color: var(--black-50);
  }

  &.active {
    background: var(--white-40);
  }
}

.close_button {
  width: 88px;
  text-align: center;
  margin: 0 auto 16px auto;
  display: block;
  position: absolute;
  will-change: auto;

  .dialog[open] & {
    --scale: 1;
    opacity: 1;
    position: absolute;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  @include mixins.md {
    margin-bottom: 60px;
    margin-left: 130px;
  }

  @include mixins.lg {
    margin-left: 160px;
  }
}

@keyframes dialog-slide-in {
  from {
    transform: translate3d(0%, 20%, -10px) scaleX(0.95) scaleY(0.9);
    opacity: 0;
  }

  to {
    transform: translate3d(0%, 0%, 0) scaleX(1) scaleY(1);
    opacity: 1;
  }
}
