@use "@/styles/mixins";

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  color: var(--black);

  &__backdrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--darker-purple-60);
  }

  &__inner {
    border-radius: 8px;
    border: 1px solid rgba(255, 255, 255, 0.3);
    background: linear-gradient(
      180deg,
      rgba(247, 247, 247, 0.8) 0%,
      rgba(247, 247, 247, 0.5) 10.69%
    );
    backdrop-filter: blur(16px);
    margin: 24px;
    padding: 40px;
    min-height: calc(100% - 16px);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: calc(100vh - 48px);
    max-width: 600px;
    animation: 0.5s ease-out 0s 1 opacity;
    z-index: 2;
    position: relative;
    max-height: calc(100svh - 48px);
    overflow: auto;
    gap: 32px;
  }

  &__closed {
    transition: opacity 0.3s ease-in, left 0s 0.3s;
    opacity: 0;
    left: -100%;
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 0;

    @include mixins.lg {
      gap: 40px;
    }
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 32px;

    button {
      display: flex;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    gap: 40px;
    align-items: flex-start;
  }

  &__logo {
    height: 30px;
  }

  &__buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;

    @include mixins.lg {
      flex-direction: row-reverse;
      justify-content: space-between;
    }

    & > * {
      flex: 1 1 0px;
    }

    a {
      padding: 0;
    }

    button {
      text-align: center;
    }
  }
}

@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
