@use "@/styles/mixins";

:root {
  --lavender: hwb(from #9d86b5 h w b);
  --lavender-40: hwb(from var(--lavender) h w b / 0.4);
  --lavender-30: hwb(from var(--lavender) h w b / 0.3);
  --lavender-10: hwb(from var(--lavender) h w b / 0.1);
  --dark-purple: hwb(from #624573 h w b);
  --darker-purple: hwb(from #260c35 h w b);
  --darker-purple-60: hwb(from var(--darker-purple) h w b / 0.6);
  --black: hwb(from #272727 h w b);
  --black-50: hwb(from var(--black) h w b / 0.5);
  --white: hwb(from #f7f7f7 h w b);
  --white-60: hwb(from var(--white) h w b / 0.6);
  --white-40: hwb(from var(--white) h w b / 0.4);
  --white-30: hwb(from var(--white) h w b / 0.3);
  --ghost: hwb(from #48235d h w b);
  --ghost-70: hwb(from var(--ghost) h w b / 0.7);
  --backdrop-bg: var(--darker-purple-60);
}

@layer base {
  * {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
  }

  html,
  body {
    max-width: 100vw;
    scroll-behavior: smooth;
  }

  html {
    background-color: #260c35;
  }

  // Break scroll if menu is open.
  html:has(#menu[aria-expanded="true"]) {
    overflow: hidden;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */

    /* Hide scrollbar for Chrome, Safari and Opera */
    &::-webkit-scrollbar {
      display: none;
    }
  }

  body:has(#menu[aria-expanded="true"]) {
    @media screen and (min-width: 768px) {
      padding-right: 15px; // Scrollbar width
    }
  }

  body {
    background: var(--body-bg);
    background-size: auto 100%;
    background-repeat: no-repeat;
    color: var(--black);

    .page-content,
    .stint-label {
      transition: all 0.3s ease;
    }
  }

  .timeline-expanded {
    overflow: hidden;

    @include mixins.lg {
      overflow: unset;
    }

    body {
      overflow: hidden;

      @include mixins.lg {
        overflow: unset;
      }
    }

    .filter-wrapper {
      display: none;

      @include mixins.lg {
        display: block;
      }
    }

    .page-content {
      transform: translateX(200px);

      @include mixins.lg {
        transform: translateX(0);
      }
    }

    .timeline-right {
      display: block !important;

      @include mixins.lg {
        display: none !important;
      }
    }

    .stint-label {
      opacity: 1 !important;
    }

    .timeline-expanded-button {
      svg {
        transform: rotate(180deg);
      }
    }
  }

  a {
    color: inherit;
    text-decoration: none;
  }

  main {
    display: grid;
    grid-template-areas: "timeline content";
    grid-template-columns: 74px 1fr;
    margin-top: 12px;
    min-height: calc(100vh - 48px);
    padding-right: 12px;

    @include mixins.lg {
      grid-template-areas: "timeline space content endspace";
      grid-template-columns: 320px 12px 1fr 12px;
      margin-top: 0;
      min-height: calc(100vh - 92px);
      padding-right: 0;
    }

    @include mixins.xl {
      grid-template-columns: 380px 54px 1fr 54px;
    }
  }

  .heading-xl {
    font-size: 28px;
    font-style: normal;
    font-weight: 600;
    line-height: 120%; /* 33.6px */
    letter-spacing: -0.84px;

    @include mixins.md {
      font-size: 40px;
      letter-spacing: -1.2px;
    }

    &.bold,
    .bold {
      font-weight: 600;
    }
  }

  .heading-l {
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 120%; /* 28.8px */
    letter-spacing: -0.72px;

    @include mixins.md {
      font-size: 24px;
    }
  }

  .heading-m {
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 120%;
  }

  .heading-s {
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 120%;
  }

  .body,
  .body-m {
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;

    @include mixins.md {
      font-size: 16px;
    }
  }

  .body-l {
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;

    @include mixins.md {
      font-size: 20px;
    }
  }

  .body-s {
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }

  .body-button {
    font-size: 12px;
    font-style: normal;
    font-weight: 500;

    @include mixins.md {
      font-size: 14px;
    }
  }

  .label {
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.56px;
    text-transform: uppercase;
  }

  .tag,
  .tag-s {
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 1px;
    text-transform: uppercase;

    @include mixins.md {
      font-size: 12px;
    }
  }

  button {
    all: unset; // Reset all styles
    cursor: pointer;
  }
}
