@use "@/styles/mixins";

.button {
  padding: 0 24px;
  cursor: pointer;
  transition: all 0.3s ease-in;
  color: white;
  pointer-events: auto;

  &.primary {
    height: 56px;
    min-height: 56px;
    padding: 0 32px;
    border-radius: 4px;
    position: relative;
    border-radius: 8px;
    border: 1px solid var(--Black-20, rgba(39, 39, 39, 0.2));
    background: var(--Lavender, #9d86b5);
    box-shadow: 0px 145px 40px 0px rgba(98, 69, 115, 0),
      0px 93px 37px 0px rgba(98, 69, 115, 0.04),
      0px 52px 31px 0px rgba(98, 69, 115, 0.13),
      0px 23px 23px 0px rgba(98, 69, 115, 0.22),
      0px 6px 13px 0px rgba(98, 69, 115, 0.26);

    @include mixins.md {
      height: 76px;
    }

    span,
    svg {
      z-index: 1;
      position: relative;
    }

    svg {
      flex: 0 0 auto;
      color: var(--white);
    }

    &:hover {
      background: var(--dark-purple);
    }
  }

  &.deletefilterbutton {
    position: relative;
    z-index: 3;
    display: flex;
    padding: 12px;
    align-items: center;
    gap: 12px;
    border-radius: 4px;
    background: var(--ghost);
  }
}

.filterbutton {
  &__wrapper {
    position: fixed;
    bottom: 1rem;
    text-align: center;
    padding: 0 32px;
    display: flex;
    align-items: center;
    gap: 8px;
    transform: translateX(-50%);
    left: 50%;
    width: max-content;

    @include mixins.md {
      left: 50vw;
      right: auto;
      bottom: 60px;
      transform: translateX(calc(-50% + 64px));
      gap: 30px;
    }

    @include mixins.xl {
      transform: translateX(calc(-50% + 70px));
    }

    a,
    button {
      align-items: center;
      display: flex;
      gap: 8px;
    }
  }

  &__content {
    display: flex;
    align-items: center;
    gap: 24px;
  }
}
