@use "@/styles/mixins";

.link {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 0 24px;
  background: transparent;
  border: 0;
  color: white;
  cursor: pointer;

  &.secondary {
    height: 45px;
    border-radius: 8px;
    border: 1px solid var(--white-60);
    backdrop-filter: blur(6px);
    color: var(--white);
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    &.active {
      background: var(--white-60);
      color: var(--black);
    }

    @include mixins.lg {
      font-size: 14px;
    }
  }

  &.tertiary {
    height: 56px;
    min-height: 56px;
    padding: 0 32px;
    border-radius: 8px;
    border: 1px solid var(--white-60);

    @include mixins.md {
      height: 76px;
    }
  }

  span {
    display: none;

    @include mixins.md {
      display: block;
      margin-left: -5px;
    }
  }
}
