@use "@/styles/mixins";

.menu {
  list-style: none;
  position: sticky;
  overflow: hidden;
  top: 0;
  left: 0;
  z-index: 10;
  padding: 18px 0;
  width: 100%;
  transition: all 200ms ease-in;

  @include mixins.lg {
    padding: 32px 0;
  }

  &.slideUp {
    transform: translateY(-70px);

    @include mixins.lg {
      transform: none;
    }
  }

  &.slideDown {
    transform: translateY(0);
  }

  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    position: relative;
  }

  &__items {
    display: grid;
    grid-template-columns: auto;
    justify-content: center;
    gap: 30px;

    @include mixins.lg {
      grid-template-columns: 320px 1fr auto;
      gap: 0;
      justify-content: start;
    }

    @include mixins.xl {
      grid-template-columns: 380px 1fr auto;
    }
  }

  &__links {
    display: flex;
    align-items: center;
    gap: 12px;
    padding-left: 12px;

    @include mixins.xl {
      padding-left: 54px;
    }
  }

  &__item {
    grid-column: 2;
  }

  &__hamburger {
    display: none;

    @include mixins.md {
      padding-right: 16px;
    }

    @include mixins.lg {
      display: flex;
      align-items: center;
      position: fixed;
      top: 32px;
      right: 0;
      z-index: 20;
    }

    @include mixins.xl {
      padding-right: 54px;
    }
  }
}

.logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
  color: var(--white);
  justify-self: center;

  @include mixins.lg {
    justify-self: start;
    padding-left: 54px;
  }

  a {
    display: flex;
  }

  &__img {
    height: 40px;

    @include mixins.md {
      height: 30px;
    }
  }
}

.button {
  display: inline-flex;
  justify-content: center;
  aspect-ratio: 1 / 1;
  border-radius: 4px;
  border: 1px solid var(--white-60);
  color: var(--white);
  align-items: center;
  height: 45px;
  box-sizing: border-box;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--backdrop-bg);
  transition: all 200ms ease-in;
  z-index: -10;
}

.list {
  background: var(--lavender);
  display: flex;
  width: calc(100vw - 1rem);
  height: calc(100dvh - 1rem);
  padding: 110px 32px 54px 32px;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  flex-shrink: 0;
  border-radius: 40px 0px 40px 40px;
  list-style: none;
  position: fixed;
  top: 0.5rem;
  right: 0.5rem;
  z-index: 12;
  max-width: 600px;

  @include mixins.md {
    padding: 110px 54px 54px 54px;
  }

  &::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
  }

  &[aria-expanded="true"] {
    transform: translateX(0);
    animation: slidein 0.6s cubic-bezier(0.215, 0.61, 0.355, 1) 0s 1 normal
      forwards;
    border-radius: 8px;
    border: 1px solid rgba(255, 255, 255, 0.3);
    background: linear-gradient(
      180deg,
      rgba(247, 247, 247, 0.8) 0%,
      rgba(247, 247, 247, 0.5) 10.69%
    );
    backdrop-filter: blur(16px);

    + .backdrop {
      display: block;
      z-index: 0;
    }
  }

  &[aria-expanded="false"] {
    animation: slideout 0.3s cubic-bezier(0.215, 0.61, 0.355, 1) 0s 1 normal
      forwards;

    + .backdrop {
      opacity: 0;
    }
  }

  &.no_animation {
    animation: none !important;
    transform: translateX(100%);
    opacity: 0;
    z-index: -1;
  }

  .list__close_item {
    position: fixed;
    top: 4px;
    right: 18px;

    button {
      padding: 32px;
      background-color: transparent;
      border: 0;
      color: var(--black);
    }

    @include mixins.md {
      bottom: unset;
      top: 0;
      right: 0;
    }
  }

  .list__center {
    display: flex;
    flex-direction: column;
    gap: 40px;
    width: 100%;
  }

  .divider {
    width: 100%;
    height: 1px;
    background-color: var(--dark-purple);
  }

  .list__main,
  .list__secondary {
    color: var(--white);
    margin-bottom: 40px;

    ul {
      display: flex;
      flex-direction: column;
      row-gap: 20px;
      list-style: none;
    }

    a {
      transition: all 150ms ease-in;
      text-underline-offset: 4px;
      text-decoration: underline;
      text-decoration-thickness: 2px;
      text-decoration-color: transparent;
      color: var(--black);
      display: flex;
      align-items: center;
      gap: 16px;
    }

    a:hover {
      text-decoration-color: currentColor;
      text-underline-offset: 6px;
    }

    .item__circle {
      flex-shrink: 0;
      background-color: var(--white-30);
      border-radius: 999px;
      height: 24px;
      width: 24px;
      aspect-ratio: 1 / 1;
      display: inline-flex;
      font-size: 1rem;
      line-height: 1;
      text-align: center;
      justify-content: center;
      align-items: center;
      box-sizing: border-box;
    }
  }

  .list__secondary {
    .item__circle {
      height: 1.5rem;
      width: 1.5rem;
      padding: 0;
      transform: translate(1rem, 2px);
    }
  }

  .list__footer {
    color: var(--black);
    span,
    a {
      color: inherit;
    }

    a {
      text-decoration: underline;
    }
  }
}

@keyframes slidein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes slideout {
  0% {
    transform: translateX(0);
    opacity: 1;
  }

  60% {
    transform: translateX(0);
    opacity: 0;
  }

  100% {
    transform: translateX(100%);
    opacity: 0;
    z-index: -1;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-100px);
  }
}

@keyframes slideDown {
  from {
    transform: translateY(-100px);
  }
  to {
    transform: translateY(0);
  }
}
